(function () {
  'use strict';

  angular
    .module('pomodoroTimer')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig/*, $mdThemingProvider*/) {
    // Enable log
    $logProvider.debugEnabled(true);

    // $mdThemingProvider
    //   .theme('default')
    //   .primaryPalette('indigo')
    //   .accentPalette('pink')
    //   .warnPalette('red')
    //   .backgroundPalette('blue-grey');

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("pomodoroTimer").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div layout=row><div flex=100><md-content layout=column><header><acme-navbar creation-date=main.creationDate></acme-navbar></header><div><div flex><div id=status class=md-display-2>Work</div><div id=counter class=md-display-4>{{main.countDownTimer|secondsToDateTime}}</div></div><div layout=row id=controls><div flex=100><md-button class=\"md-raised md-primary\" ng-click=main.playTimer()>Play</md-button><md-button class=\"md-raised md-primary\" ng-click=main.pauseTimer()>Pause</md-button><md-button class=\"md-raised md-warn\" ng-click=main.resetTimer()>Reset</md-button></div></div></div></md-content></div></div>");
$templateCache.put("app/components/navbar/navbar.html","<md-toolbar layout=row layout-align=\"center center\"><md-button href=#>Pomodoro Timer</md-button><section flex layout=row layout-align=\"left center\"><!--<md-button href=\"#\" class=\"md-raised\">Home</md-button>\r\n    <md-button href=\"#\" class=\"md-raised\">About</md-button>\r\n    <md-button href=\"#\" class=\"md-raised\">Contact</md-button>--></section><md-button class=acme-navbar-text>Application was created {{ vm.relativeDate }}.</md-button></md-toolbar>");}]);